.my-div {
    
    /* Couleur de fond initiale */
    padding: 20px;
transition: background-color 0.3s, border-radius 0.3s;
    /* Ajout d'une transition pour une animation fluide */
    border-radius: 10px;
    /* Dfinition du rayon des coins */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* Ajout d'une ombre */
    margin-bottom: 20px;
        /* Ajout de la marge en bas */
    margin-left: inherit;
    background-color: #ffffff;
}

.my-div.hovered {
    background-color: #f0f0f0;
    /* Couleur de fond au survol */
    color: #fff;
    /* Couleur du texte au survol */
    border-radius: 20px;
        /* Rayon des coins au survol */
        
}

/* Pour les petits crans */
@media (max-width: 767.98px) {
    .my-div {
        padding: 0;
        /* Padding de 0 pour les petits crans */
    }
}